<template>
  <transition name="dialog-fade" @after-enter="afterEnter" @after-leave="afterLeave">
    <div class="in-dialog__wrapper" v-show="visible" @click.self="handleClose">
        <div class="in-dialog" :style="{width: width, marginTop: top}">
          <div class="in-dialog__header">
            <slot name="title">
              <span class="in-dialog__title">{{title}}</span>
            </slot>
            <button class="in-dialog__headerbtn" @click="handleClose">
              <i class="in-icon-close"></i>
            </button>
          </div>
          <div class="in-dialog__body">
            <!-- 默认插槽 -->
            <slot></slot>
          </div>
          <div class="in-dialog__footer" v-if="$slots.footer">
            <slot name="footer"></slot>
          </div>
        </div>
      </div>
  </transition>
</template>

<script>
export default {
  name: 'InDialog',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '50%'
    },
    top: {
      type: String,
      default: '15vh'
    },
    title: {
      type: String,
      default: '提示'
    }
  },
  methods: {
    handleClose () {
      this.$emit('update:visible', false)
    },
    afterEnter () {
      this.$emit('opened')
    },
    afterLeave () {
      this.$emit('closed')
    }
  }
}

</script>
